<template>
  <div>
    <!-- /*//导航部分开始*/-->
    <div :class="[currentClass,{isFixed:sideFixed}]">
      <div class="h_navLeft">
        <a v-if="widthH<768" class="more" @click="showIndex">
          <img src="@/assets/prublic/gengduo.png">
        </a>
        <a href="javascript:0" class="logo" @click="tabsChange('/index/home')">
          <img src="@/assets/prublic/logo.png">
        </a>
        <a v-if="widthH>=768" class="more" @mouseenter="menuShow">
          <img src="@/assets/prublic/gengduo.png">
        </a>
      </div>
      <div class="h_navCenter" v-if="widthH<768">
        <div class="h_navSearch">
          <a href="javascript:0;">
            <router-link to="/search">
              <img src="@/assets/prublic/sousuo.png">
            </router-link>
          </a>
          <input
            type="text"
            name
            id="h_navSearchText"
            value
            placeholder="SUNGLASSES"
            @focus="animateWidth()"
            @input="getInput()"
            v-model="searchInput"
          >
        </div>
      </div>
      <div class="h_navCenter" v-if="widthH >= 768">
        <div class="search" v-if="showSearch&&route_pash=='/search'">
          <el-input class="input" placeholder="SUNGLASSES" v-model="searchInput"></el-input>
          <span class="btn" @click="getInput()">SEARCH</span>
        </div>
        <span
          v-else
          :class="{active:currrntTab===index}"
          v-for="(item,index) in menuList"
          :key="index"
          @click="tabsChange(item.url,index,item.categoryId)"
          @mouseenter="menuShow(index)"
        >{{item.categoryName}}</span>
      </div>
      <div class="h_navRight">
        <a href="javascript:0" v-if="widthH>=768">
          <router-link :to="{path:'/search'}">
            <img src="@/assets/prublic/sousuo.png" @click="getInput()">
          </router-link>
        </a>
        <a href="javascript:0">
          <router-link to="/cart">
            <img src="@/assets/prublic/gouwu.png">
          </router-link>
        </a>
        <a href="javascript:0">
          <router-link to="/myWish">
            <img src="@/assets/prublic/shoucan.png">
          </router-link>
        </a>
      </div>
      <!-- 下拉菜单 开始-->
      <div v-if="dropdown" :class="currentClass2" @mouseleave="menuHidde">
        <div class="menu">
          <div class="menuBigLeft">
            <span
              v-for="(item,index) in menuList[currrntTab].cateGoryList"
              :key="index"
              @click="jump(item.categoryId,currrntTab)"
              @mouseover="hoverIndex = index"
              @mouseout="hoverIndex = -1"
              :class="{'clickBg':index==clickIndex,'hoverBg':index==hoverIndex}"
            >{{item.categoryName}}</span>
          </div>
        </div>
      </div>
      <!-- 下拉菜单 结束-->
    </div>
    <!-- 手机抽屉 -->
    <div class="index_10" v-if="index_10">
      <span
        v-for="(item,index) in menuList[0].cateGoryList"
        :key="index"
        @click="jump(item.categoryId)"
      >{{item.categoryName}}</span>
    </div>
    <!-- 手机白色分类导航 -->
    <div
      class="h_nav22"
      v-if="widthH<768&&route_pash!='/index/productDetail'&&route_pash!='/search'"
    >
      <div class="h_nav2Content">
        <span
          :class="{active:currrntTab===index}"
          v-for="(item,index) in bannerList"
          :key="index"
          @click="tabsChange(item.url,index,item.data)"
        >{{item.name}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getHomeNav,
  activityX,
  getProductCategory
} from "../../Ajax/modules/home";
import { isNumber } from "util";
export default {
  data() {
    return {
      showSearch: false,
      searchInput: "",
      widthH: "",
      dropdown: false,
      login: "Sign In",
      registered: "registered",
      //导航2
      //下拉菜单分类
      menuList: [
        { categoryName: "DEAL", url: "/index/deal" },
        { categoryName: "TRENDS", url: "/index/bolg" }
      ],
      route_pash: "",
      //店铺名称
      shopNameList: [
        "Logo custom",
        "Barcode printing",
        "Packaging",
        "Customs clearance service",
        "Glasses accessories"
      ],
      //店铺分类
      shopList: ["BRAND EYE STORE", "OPTICAL SHOP"],
      currrntTab: "", //当前tab
      sideFixed: false, //顶部是否固定
      hoverIndex: -1, //表示当前hover的是第几个li 初始为 -1 或 null 不能为0 0表示第一个li
      clickIndex: -1, //表示当前点击的是第几个li 初始为 -1 或 null 不能为0 0表示第一个li
      clickShop: 0, //表示当前点击的是第几个li 初始为 -1 或 null 不能为0 0表示第一个li
      clickBorder: 0, //表示当前点击的是第几个li 初始为 -1 或 null 不能为0 0表示第一个li
      index_10: false,
      isShowZd: ""
    };
  },
  created() {
    this.widthH = this.$root.widthH;
    this.getProductCategory();
  },
  mounted() {
    //添加滚动事件
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    //离开页面销毁
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    //下拉菜单
    getProductCategory() {
      getProductCategory().then(res => {
        if (res.data.code == 0) {
          res.data.data.forEach(item => {
            Object.assign(item, { url: "/search" });
          });
          console.log(res.data.data);
          this.menuList = [...res.data.data, ...this.menuList];
        }
      });
    },
    //导航路由切换
    tabsChange(url, index, data) {
      this.showSearch = false;
      if (isNumber(index)) {
        this.currrntTab = index;
      }
      if (url == "/index/deal") {
        activityX().then(res => {
          console.log(res);
          if (res.data.code == 0) {
            if (!res.data.data) {
              this.$router.push({
                path: "/search",
                query: {
                  name: "discount"
                }
              });
            } else {
              this.$router.push(url);
            }
          }
        });
      } else {
        if (data||data==0) {
          this.$router.push({
            path: url,
            query: {
              data,
              tabId:index
            }
          });
        } else {
          this.$router.push(url);
        }
      }
    },
    //顶部滚动监听
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 30) {
        this.sideFixed = true;
      } else {
        this.sideFixed = false;
      }
    },
    //菜单显示隐藏
    menuShow(index) {
        this.currrntTab = index;
      if (index == 0 || index == 1 || index == 2) {
        this.dropdown = true;
        if (isNumber(index)) {
          this.currrntTab = index;
          this.isShowZd = index;
        } else {
          this.isShowZd = "";
        }
      }
    },
    menuHidde() {
      this.dropdown = false;
    },
    //手机页面抽屉显示隐藏
    showIndex() {
      this.index_10 = !this.index_10;
    },
    //菜单左侧导航跳转
    jump(id,tabId) {
      this.index_10 = false;
      this.$router.push({
        path: "/search",
        query: {
          data: id,
          tabId:tabId
        }
      });
    },
    animateWidth() {
      this.$router.push("/search");
    },
    getInput() {
      (this.showSearch = true), this.bus.$emit("getInput", this.searchInput);
    },
    //点击改变店铺分类
    changeShopNameList() {
      // this.shopNameList = ""; //传入新数据
    },
    isShowSearch() {
      this.showSearch = !this.showSearch;
    }
  },
  computed: {
    currentClass() {
      if (this.widthH >= 768) {
        return "h_nav1";
      } else {
        return "h_nav2";
      }
    },
    currentClass2() {
      if (this.isShowZd == "") {
        return "dropdown1";
      } else if (this.isShowZd == 1) {
        return "dropdown2";
      } else if (this.isShowZd == 2) {
        return "dropdown3";
      } else if (this.isShowZd == 3) {
        return "dropdown4";
      } else if (this.isShowZd == 4) {
        return "dropdown5";
      }
    }
  },
  watch: {
    //动态获取路由路径
    $route(to, from) {
      this.route_pash = this.$route.path;
      console.log(this.$route.path);
    }
  }
};
</script>
<style  lang="scss" scoped>
/*//导航部分开始*/
