.dropdown1 {
  width: 40%;
  height: 400px;
  box-shadow: 2px 2px 2px #999999;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 1);
  padding: 0 10px;
  box-sizing: border-box;
  position: absolute;
  left: 10%;
  z-index: 99999;
  overflow: hidden;
  animation: slowDown1 0.7s;
  .menu {
    height: 400px;
    .menuBigLeft {
      // width: 200px;
      background-color: rgba(255, 255, 255, 0.9);
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      // justify-content: center;
      padding-right: 30px;
      height: 100%;
      font-size: 16px;
      font-weight: bold;
      font-family: "regular";
      span {
        text-align: center;
        height: 60px;
        width: 30%;
        line-height: 60px;
        padding: 10px 20px;
        cursor: pointer;
      }
      span:hover {
        background-color: #999999;
      }
    }
    .menuBigCenter {
      .shopList {
        line-height: 40px;
        span {
          margin-right: 20px;
          color: #666666;
          cursor: pointer;
          &.activeColor {
            color: #231815;
          }
        }
      }
      .shopNameList {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        span {
          display: inline-block;
          border: 1px solid #666666;
          color: #666666;
          padding: 0px 10px;
          margin-bottom: 20px;
          margin-right: 10px;
          height: 40px;
          line-height: 40px;
          cursor: pointer;
          &.activeBorder {
            border: 1px solid #231815;
            color: #231815;
          }
        }
      }
      .yanjingList {
        display: flex;
        justify-content: flex-start;
        .yanjing {
          margin-right: 20px;
          text-align: center;
          cursor: pointer;
          p {
            height: 20px;
          }
          img {
            width: 80px;
            height: 80px;
          }
        }
      }
    }
    .menuBigRight {
      ul {
        li {
          display: block;
          width: 80%;
          padding-bottom: 14px;
          position: relative;
          span {
            position: absolute;
            width: 100%;
            text-align: center;
            left: 50%;
            transform: translateX(-50%);
            bottom: 10px;
            padding-bottom: 14px;
            color: #fff;
          }
          img {
            display: block;
            width: 300px;
            height: 150px;
          }
        }
      }
    }
  }
}
