.h_nav1 {
  width: 100%;
  position: relative;
  background-color: #eae62d;
  color: #231815;
  font-size: 16px;
  padding: 0px 50px;
  box-sizing: border-box;
  &.isFixed {
    position: fixed;
    top: 30px;
    z-index: 9999;
  }
  .h_navLeft {
    float: left;
    display: flex;
    line-height: 80px;
    .logo {
      display: inline-block;
      width: 156px;
      height: 57px;
    }
    .more {
      display: inline-block;
      // width: 24px;
      // height: 16px;
    }
    a {
      img {
        width: 100%;
        vertical-align: middle;
      }
    }
    a:first-child {
      margin-right: 49px;
    }
  }
  .h_navCenter {
    height: 80px;
    line-height: 80px;
    margin-right: 10%;
    padding: 0px 100px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    font-family: "Regular";
    font-weight: 500;
    font-size: 16px;
    color: #231815;
    .search {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      .img {
        width: 40px;
        height: 40px;
        background-image: url("../../assets/search/search.png");
        background-repeat: no-repeat;
        background-position: center;
      }
      .btn {
        height: 40px;
        line-height: 40px;
        width: 100px;
        color: #eae62d;
        text-align: center;
        border-radius: 2px;
        background-color: #231815;
      }
      .input {
        width: 800px;
      }
    }
    span {
      color: #231815;
      cursor: pointer;
      position: relative;
      &::after {
        content: "";
        height: 4px;
        width: 100%;
        background-color: #231815;
        position: absolute;
        bottom: 0;
        left: 0;
        transition: all 0.2s linear;
        transform: scaleX(0);
      }
      &.active {
        color: #231815;
        font-weight: bold;
        &::after {
          transform: scaleX(1);
        }
      }
    }
  }
  .h_navRight {
    line-height: 80px;
    position: absolute;
    width: 10%;
    right: 20px;
    top: 0px;
    display: flex;
    justify-content: space-around;
    a {
      display: inline-block;
      width: 20px;
      height: 20px;
      img {
        width: 100%;
      }
    }
  }
}
.h_nav2 {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  position: relative;
  background-color: #eae62d;
  color: #231815;
  font-size: 0.16rem;
  padding: 0rem 0.3rem;
  box-sizing: border-box;
  z-index: 1;
  .h_navLeft {
    float: left;
    display: flex;
    line-height: 0.9rem;
    .logo {
      display: inline-block;
      width: 1.56rem;
      height: 0.57rem;
    }
    .more {
      display: inline-block;
      width: 0.24rem;
      height: 0.16rem;
    }
    a {
      img {
        width: 100%;
        vertical-align: middle;
      }
    }
    a:first-child {
      margin-right: 0.4rem;
    }
  }
  .h_navCenter {
    position: absolute;
    top: 0;
    left: 2.2rem;
    width: 3.6rem;
    height: 0.9rem;
    line-height: 0.9rem;
    padding-left: 0.3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: "Regular";
    font-size: 0.16rem;
    color: #231815;
    .h_navSearch {
      display: flex;
      height: 0.5rem;
      background-color: #fff;
      border-radius: 0.02rem;
      padding-left: 0.14rem;
      a {
        display: flex;
        width: 0.2rem;
        height: 0.5rem;
        align-items: center;
        img {
          width: 100%;
        }
      }
      #h_navSearchText {
        padding-left: 0.1rem;
        font-size: 0.18rem;
      }
    }
  }
  .h_navRight {
    position: absolute;
    right: 0.5rem;
    top: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 15%;
    a {
      display: inline-block;
      width: 0.2rem;
      height: 0.2rem;
      img {
        width: 100%;
      }
    }
    a:first-child {
      padding-left: 0.3rem;
    }
  }
}
.h_nav22 {
  width: 100%;
  height: 0.58rem;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
}
.h_nav22 .h_nav2Content {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 100%;
  font-size: 0.16rem;
  font-family: "regular";
  font-weight: bold;
}
.h_nav22 .h_nav2Content span {
  height: 100%;
  box-sizing: border-box;
  line-height: 0.58rem;
}
.h_nav22 .h_nav2Content .active {
  border-bottom: 2px solid #eae62d;
}
.index_10 {
  position: fixed;
  top: 0;
  right: 0;
  width: 70%;
  height: 100%;
  z-index: 9999999;
  background-color: rgba(255, 255, 255, 0.94);
  display: flex;
  flex-direction: column;
  padding: 1rem 0.45rem;
  font-size: 0.22rem;
  animation: index_10 0.7s;
  overflow: hidden;
  span {
    margin-top: 0.6rem;
  }
}
@keyframes index_10 {
  from {
    width: 0%;
  }
  to {
    width: 70%;
  }
}
/*导航部分结束*/
</style>




